export default {
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      userId: ''
    }
  },
  created () {
    this.userId = this.$route.params.userId
    console.log(this.userId)
  },
  methods: {
    changeStatus (num) {
      switch (num) {
        case 0:
          return ''
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ''
        case 4:
          return ''
        case 5:
          return ''
      }
    },
    changePrice (item) {
      switch (item.orderStatus) {
        case 0:
          return ''
        case 1:
          return '0.00'
        case 2:
          return '' + item.maxFee
        case 3:
          return '' + item.maxFee
        case 4:
          return '' + item.maxFee
        case 5:
          return ''
      }
    },
    handleSaveData (item) {
      this.$localStorage.set('orderDetail', JSON.stringify(item))
    }
  }
}



